<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Verify mobile number</h1>
      <div class="chartWrap"></div>
    </section>
    <div class="centerWrap">
      <div class="center">
        <span class="title">AE +{{username}}<a href='javascript:void(0)' @click="$router.go('-1')">(Change)</a></span>
        <p>
          A text with a code has been sent to the number above, please enter the
          code below.
        </p>
        <div class="emailWrap">
          <img src="../common/images/orders.png" />
          <van-field placeholder="Enter Verify Code" />
        </div>
        <van-button type="danger"@click="submitSms">{{btnText}}</van-button>
        <span class="again"
          >Didn't receive the code ? <a href='javascript:void(0)' @click="sendSms">(Resend Code)</a></span>
        
      </div>
    </div>
  </div>
</template>

<script>
import { sendSms } from "@/api/msg";
export default {
  name: "verifyMobile",
  data() {
    return {
      btnText: "",
      username: this.$route.query.username,
      type: this.$route.query.type
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (this.type == "forget"||this.type=='signup') {
        this.btnText = "Verify";
      } else {
        this.btnText = "Sign In";
      }
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    submitSms() {
        if(this.type=='forget'){
        this.$router.push({path:'setPwd',query:{username:this.username}});
      }else  if(this.type=='signup'){
        this.register();
      }else{
        this.btnText="Sign In";
      }
    },
    sendSms() {
      sendSms(this.username, this.type)
        .then(res => {
          this.$toast("Code send successfully");
        })
        .catch(e => {
          this.$toast(e.message);
        });
    } ,
     register(){
      let param=JSON.parse(this.$route.query.extra);
        this.$store.dispatch('register',param).then(r=>{
           this.$toast(' Sign up successfully');
           
        });
    },
  }
};
</script>

<style lang="scss" scoped>
.centerWrap {
  width: 100%;
  padding-top: 10px;
  background: rgba(246, 246, 246, 1);
}
.center {
  width: 100%;
  padding: 0 4%;
  background-color: #fff;
  font-size: 13px;
}
.center p {
  width: 100%;
  padding: 0 6%;
  color: #7d7e80;
}
.title {
  width: 100%;
  text-align: center;
  display: block;
  font-size: 16px;
  padding: 10px 0 10px 0;
}
.again {
  width: 100%;
  text-align: center;
  display: block;
  font-size: 13px;
  color: #7d7e80;
  padding-top: 10px;
}
.title a {
  font-size: 12px;
  color: #2b87d5;
}
.again a {
  color: #2b87d5;
}
.center .van-cell {
  border-bottom: 1px solid #f4f4f4;
  width: 95%;
  padding: 10px 0;
}
.center .van-button--danger {
  width: 100%;
}
.emailWrap {
  width: 100%;
  padding: 20px 6% 70px 6%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.emailWrap img {
  width: 19px;
  height: 19px;
  margin-right: 5px;
}
</style>



